<template> 
    <!-- Main content -->    
      <v-container grid-list-sm fluid>
        <!-- Familii -->
         <v-layout row wrap>
           <v-flex xs6>
              <v-card>
             <v-card-title>Status familii</v-card-title>
             <v-card-text>
                <!-- grafic -->    
              <div class="row GraphContainer">      
                <div class="col m12 s12">
                  <center>
                  <pie-chart :data="fams" :colors="['#a0cfff', '#FFC107', '#69c56c']" :donut="true" legend="bottom" ></pie-chart>
                </center>
                </div>
              </div>    
             </v-card-text>
           </v-card> 
           </v-flex>
           <v-flex xs6>
           <v-card>
             <v-card-title>Status invitati</v-card-title>
             <v-card-text>
                <!-- grafic -->    
              <div class="row GraphContainer">      
                <div class="col m12 s12">
                  <center>
                  <pie-chart :data="invitati" :colors="['#a0cfff', '#FFC107', '#69c56c']" :donut="true" legend="bottom" ></pie-chart>
                </center>
                </div>
              </div>    
             </v-card-text>
           </v-card> 
           </v-flex>
         </v-layout>           
      </v-container>    
</template>

<script>


  export default {
    data(){
      return{
         chartData:{"in progres":2,"in progress":4},
          //  ["done",3]
          //  ]
      }
    },
    computed:{
      fams(){
        const data=this.$store.getters.getGroupStatus
        
        var chart={}
        for (let key in data) {          
          chart[key]=data[key].length
        }

        return chart
        // return this.$store.getters.getGraph_groupStatus
      },
      invitati(){
        const toti=this.$store.getters.getInvitati
        const nesositi=this.$store.getters.getIndivNesositi
        var chart={}
        chart["Veniti"]=toti.length-nesositi.length
        chart["Neveniti"]=nesositi.length

        return chart
      }
    }
  //   created() {
  //  
  // },
  }
</script>


